$imgWidth: 200px;

.text{
    display: flex;
    color: var(--black);
    justify-content: center;
}

#projects{
    display: flex;
    justify-content: center;
    width: 100%;
    height: auto;
    background-color: var(--lightWhite);
    transition: background-color .5s ease-in-out;
    margin-top: 100px;

    @include midScreen{
        margin-top: 150px;
    }
    .work-container{
        width: 100%;
        max-width: 1640px;

        .work-header{
            display: flex;
            justify-content: center;
            flex-direction: column;
            margin-top: 100px;

            .work-title{
                font-weight: 700;
                font-size: clamp(1.8rem, 1.25vw, 2rem);
                letter-spacing: 1px;
                color: var(--black);
                text-shadow: rgba(0, 0, 0, 0.25) 1.95px 1.95px 2px;
            }
            
            .work-desc{
                font-weight: 500;
                font-size: clamp(1.25rem, 1.25vw, 1.5rem);
                letter-spacing: 1px;
                color: var(--black);
                text-shadow: rgba(0, 0, 0, 0.25) 1.95px 1.95px 2px;
                @include underline(40px);
            }
        }

        .work-grid{
            display: flex;
            margin-top: 75px;
            overflow: auto;
            padding: 0px 15px 125px 15px;

            .work-card{
                cursor: pointer;
                display: flex;
                flex-direction: column;
                align-items: center;
                background-color: var(--white);
                box-shadow: 15px 15px var(--darkWhite);
                text-decoration: none;
                width: 300px;
                margin: 25px 25px;
                transition: transform .2s ease-in-out, background-color .5s ease-in-out, box-shadow .5s ease-in-out !important;
                
                @include smallScreen{
                    box-shadow: 20px 20px var(--darkWhite);
                }
                @include midScreen{
                    width: 350px;
                }
                @include largeScreen{
                    box-shadow: 25px 25px var(--darkWhite);
                }
                @include xlargeScreen{
                    width: 400px;
                }
                @include leftEffect;

                &:hover{
                    transform: scale(1.05);
                }
                img{
                    width: 300px;
                    height: auto;

                    @include midScreen{
                        width: 350px;
                    }
                    @include xlargeScreen{
                        width: 400px;
                    }
                }
                .card-text{
                    padding: 15px;

                    h4{
                        font-weight: 700;
                        font-size: clamp(1.2rem, 2vw, 1.3rem);
                        color: var(--darkGrey);
                        @include textShadow;

                    }
                    p:nth-child(2){

                        font-weight: 400;
                        font-size: clamp(.8rem, 2vw, .9rem);
                        color: var(--darkGrey);
                        @include textShadow;
                    }
                    p:last-child{
                        font-family: 'Poppins', sans-serif;
                        font-weight: 400;
                        font-size: clamp(.9rem, 2vw, 1rem);
                        color: var(--grey);
                        margin-top: 15px;
                    }
                }
            }
        }
    }
}