*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Montserrat', sans-serif;
}
#app{
    background-color: var(--white);
    --darkWhite: rgba(235, 235, 235, 1);
    --white: rgba(255, 255, 255, 1);
    --lightWhite: rgba(245, 245, 245, 1);

    --darkGrey: rgba(40 ,40 ,40, 1);
    --grey: rgba(60, 60, 60, 1);
    --lightGrey: rgba(80, 80, 80, 1);

    --black: rgba(0, 0, 0, 1);

    --sW: rgba(255, 255, 255, .8);
    --sG: rgba(40 ,40 ,40, 1);
    transition: all .5s ease-in-out;
    &.dark{
        --darkWhite: rgba(70, 70, 70, 1);
        --white: rgba(60, 60, 60, 1);
        --lightWhite: rgba(65, 65, 65, 1);

        --darkGrey: rgba(235, 235, 235, 1);
        --grey: rgba(255, 255, 255, 1);
        --lightGrey: rgba(245, 245, 245, 1);

        --black: rgba(255, 255, 255, 1);

        --sW: rgba(60, 60, 60, .8);
        --sG: rgba(35 ,35 ,35, 1);
    }
}
html{
    scroll-behavior: smooth;
}
body{
    overflow-x: hidden;
}

.expand-btn{
    cursor: pointer;
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    left: 0;
    right: 0;
    margin: 0 auto;
    background: none;
    
    color: var(--darkGrey);
    border: 0px;
    width: 100px;
    height: 75px;

    span:first-child{
        position: absolute;
        margin-bottom: 35px;
        font-weight: 400;
        font-size: clamp(.60rem, 1.25vw, .80rem);
    }
    span:last-child{
        display: flex;
        align-items: center;
        font-size: clamp(3rem, 2vw, 3.2rem);
        transition: transform .25s ease-in-out;
    }
    &:hover{
        span:last-child{
            transform: translateY(10px);
        }
    }
    &.active{
        span:last-child{
            transform: rotate(180deg) translateY(-10px);
        }
        &:hover{
            span:last-child{
                transform: rotate(180deg) translateX(0px);
            }
        }
    }
}

//Permanent colors
$white: rgba(255, 255, 255, 1);
$lightWhite: rgba(235, 235, 235, 1);
$black: rgba(0, 0, 0, 1);
$darkGrey: rgba(40 ,40 ,40, 1);
$grey: rgba(60, 60, 60, 1);
$lightGrey: rgba(80, 80, 80, 1);

//Day sky
$blue: rgba(0, 147, 233, 1);
$blue-gradient: linear-gradient(to bottom, rgba(0, 147, 233, 1) 0%, rgba(128, 208, 199, 1) 100%);
$blue-gradient2: linear-gradient(to top, rgba(0, 147, 233, 1) 0%, rgba(128, 208, 199, 1) 100%);

//Night sky
$darkBlue: rgba(0, 4, 40, 1);
$darkBlue-gradient: linear-gradient(to bottom, rgba(0, 4, 40, 1) 0%, rgba(0, 78, 146, 1) 100%);
$darkBlue-gradient2: linear-gradient(to top, rgba(0, 4, 40, 1) 0%, rgba(0, 78, 146, 1) 100%);

@mixin leftEffect{
    transform: translateX(-200px);
    opacity: 0;
    transition: transform .75s cubic-bezier(0,0.5,0,1) 0s, opacity .75s cubic-bezier(0,0.5,0,1) 0s;

    &.reveal{
        transform: translateX(0px);
        opacity: 1;
        transition: transform 1s cubic-bezier(0,0.5,0,1) .25s, opacity 1s cubic-bezier(0,0.5,0,1) .25s;
    }
}
@mixin bottomEffect{
    transform: translateY(200px);
    opacity: 0;
    transition: transform .75s cubic-bezier(0,0.5,0,1) 0s, opacity .75s cubic-bezier(0,0.5,0,1) 0s;

    &.reveal{
        transform: translateY(0px);
        opacity: 1;
        transition: transform 1s cubic-bezier(0,0.5,0,1) .25s, opacity 1s cubic-bezier(0,0.5,0,1) .25s;
    }
}
@mixin textShadow{
    text-shadow: rgba(0, 0, 0, 0.25) 1.95px 1.95px 2px;
}
@mixin underline($top){
    display: flex;
    justify-content: center;
    
    &::before{
        content: "";
        position: absolute;
        width: 50px;
        height: 2px;
        background: rgba(0, 147, 233, .8);
        margin-top: $top;
    }

    &::after{
        content: "";
        position: absolute;
        width: 25px;
        height: 2px;
        background: rgba(0, 147, 233, .6);
        margin-top: $top + 7px;
    }
}

@mixin smallScreen{
    @media screen and (min-width: 480px){
        @content;
    }
}
@mixin midScreen{
    @media screen and (min-width: 1025px){
        @content;
    }
}
@mixin largeScreen{
    @media screen and (min-width: 1200px){
        @content;
    }
}
@mixin xlargeScreen{
    @media screen and (min-width: 2000px){
        @content;
    }
}

