* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Montserrat", sans-serif;
}

#app {
  background-color: var(--white);
  --darkWhite: rgba(235, 235, 235, 1);
  --white: rgba(255, 255, 255, 1);
  --lightWhite: rgba(245, 245, 245, 1);
  --darkGrey: rgba(40 ,40 ,40, 1);
  --grey: rgba(60, 60, 60, 1);
  --lightGrey: rgba(80, 80, 80, 1);
  --black: rgba(0, 0, 0, 1);
  --sW: rgba(255, 255, 255, .8);
  --sG: rgba(40 ,40 ,40, 1);
  transition: all 0.5s ease-in-out;
}
#app.dark {
  --darkWhite: rgba(70, 70, 70, 1);
  --white: rgba(60, 60, 60, 1);
  --lightWhite: rgba(65, 65, 65, 1);
  --darkGrey: rgba(235, 235, 235, 1);
  --grey: rgba(255, 255, 255, 1);
  --lightGrey: rgba(245, 245, 245, 1);
  --black: rgba(255, 255, 255, 1);
  --sW: rgba(60, 60, 60, .8);
  --sG: rgba(35 ,35 ,35, 1);
}

html {
  scroll-behavior: smooth;
}

body {
  overflow-x: hidden;
}

.expand-btn {
  cursor: pointer;
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  left: 0;
  right: 0;
  margin: 0 auto;
  background: none;
  color: var(--darkGrey);
  border: 0px;
  width: 100px;
  height: 75px;
}
.expand-btn span:first-child {
  position: absolute;
  margin-bottom: 35px;
  font-weight: 400;
  font-size: clamp(0.6rem, 1.25vw, 0.8rem);
}
.expand-btn span:last-child {
  display: flex;
  align-items: center;
  font-size: clamp(3rem, 2vw, 3.2rem);
  transition: transform 0.25s ease-in-out;
}
.expand-btn:hover span:last-child {
  transform: translateY(10px);
}
.expand-btn.active span:last-child {
  transform: rotate(180deg) translateY(-10px);
}
.expand-btn.active:hover span:last-child {
  transform: rotate(180deg) translateX(0px);
}

header {
  display: flex;
  justify-content: center;
  position: absolute;
  width: 100%;
  z-index: 30;
  transition: transform 0.01s;
}
header .indicator {
  position: absolute;
  background-color: rgb(255, 255, 255);
  width: 0px;
  height: 2px;
  bottom: 8px;
}
header .indicator.sticky {
  bottom: 26px;
}
@media screen and (max-width: 1024px) {
  header .indicator {
    background-color: transparent;
  }
}
header.sticky {
  padding: 5px 0px 17px 0px;
  margin-top: -101px;
  position: fixed;
  transition: background-color 0.1s ease-in-out;
}
@media screen and (max-width: 1024px) {
  header.sticky {
    padding: 0px 0px 65px 0px;
    height: 50px;
  }
}
header.sticky .hamburger-menu .menu-lines {
  background-color: var(--white);
}
header.sticky .hamburger-menu .menu-lines::before {
  background-color: var(--white);
}
header.sticky .hamburger-menu .menu-lines::after {
  background-color: var(--white);
}
@media screen and (min-width: 1025px) {
  header.sticky ul .list a {
    color: var(--white);
  }
  header.sticky ul .mode {
    background-image: linear-gradient(to left, var(--white) 50%, transparent 50%);
    border-color: var(--white);
  }
  header.sticky ul .mode .sun-icon {
    color: var(--white);
  }
  header.sticky ul .mode .moon-icon {
    color: var(--white);
  }
  header.sticky ul .mode:hover .sun-icon {
    color: var(--darkGrey);
  }
  header.sticky ul .mode:hover .moon-icon {
    color: var(--darkGrey);
  }
}
header #nav-bar {
  width: 100%;
  max-width: 1440px;
  top: 0;
  transition: top 0.1s ease-in-out;
}
@media screen and (max-width: 1024px) {
  header #nav-bar .hamburger-menu {
    margin-top: 305px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    width: 60px;
    height: 60px;
    z-index: 21;
    right: 5px;
  }
  header #nav-bar .hamburger-menu .menu-lines {
    background-color: rgb(255, 255, 255);
    width: 30px;
    height: 2px;
    transition: transform 0.3s ease-in-out, background-color 0.3s ease-in-out;
  }
  header #nav-bar .hamburger-menu .menu-lines::before {
    content: "";
    position: absolute;
    background-color: rgb(255, 255, 255);
    width: 30px;
    height: 2px;
    transform: translateY(10px);
    transition: transform 0.3s ease-in-out, background-color 0.3s ease-in-out;
    width: 20px;
    margin-left: 10px;
  }
  header #nav-bar .hamburger-menu .menu-lines::after {
    content: "";
    position: absolute;
    background-color: rgb(255, 255, 255);
    width: 30px;
    height: 2px;
    transform: translateY(-10px);
    transition: transform 0.3s ease-in-out, background-color 0.3s ease-in-out;
  }
  header #nav-bar .hamburger-menu .menu-lines.active {
    background-color: var(--darkGrey);
    transform: rotate(-45deg);
  }
  header #nav-bar .hamburger-menu .menu-lines.active::before {
    background-color: var(--darkGrey);
    transform: scaleX(0.01);
  }
  header #nav-bar .hamburger-menu .menu-lines.active::after {
    background-color: var(--darkGrey);
    transform: rotate(90deg);
  }
  header #nav-bar ul {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 0;
    margin-top: -300px;
    background-color: var(--sW);
    -webkit-backdrop-filter: blur(5px);
            backdrop-filter: blur(5px);
